@use '../util' as *;

.hero {
  width: 100%;
  margin-bottom: rem(64);

  &__background {
    background-position: center;
    background-size: cover; 
    left: 50%; 
    padding: 0; 
    position: relative;
      -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    margin-top: 0px; 
    margin-bottom: 0px; 
    height: rem(350);
    overflow: hidden; 
    clear: both;
  }

  &__logo {
    margin-bottom: rem(60);

    @include breakpoint-down (small) {
      padding: 0 rem(20);
    }
  }

  &__headline {
    display: flex;
    // justify-content: center;
    align-items: center;
    flex-direction: column;
    position: absolute;
    top: 65%;
    z-index: 900;
    width: 100%;
    // height: 100vh;
  }

  h1 {
    color: var(--white);
    font-size: rem(64);
    font-weight: 400;
    line-height: 1;
    text-align: center;
    text-shadow: 1px 1px 2px var(--black);
    margin-bottom: rem(15);

    @include breakpoint-down(small) {
      font-size: rem(42);
    }
  }

}